import React, { useState } from "react";
import Items from "./Items.jsx";

function Bar(props) {
  const [menu, setMenu] = useState([...props.data]);

  const forAll = () => {
    setMenu([...props.data]);
  };
  const forBreakfasts = () => {
    setMenu(props.data.filter((el) => el.category === "breakfast"));
  };
  const forLunch = () => {
    setMenu(props.data.filter((el) => el.category === "lunch"));
  };
  const forShakes = () => {
    setMenu(props.data.filter((el) => el.category === "shakes"));
  };

  return (
    <div>
      <h1 className="main-title">Our Menu</h1>
      <div className="underline"></div>
      <div className="btn-container">
        <button className="filter-btn" onClick={forAll}>
          All
        </button>
        <button className="filter-btn" onClick={forBreakfasts}>
          Breakfast
        </button>
        <button className="filter-btn" onClick={forLunch}>
          Lunch
        </button>
        <button className="filter-btn" onClick={forShakes}>
          Shakes
        </button>
      </div>
      <Items list={menu} />
    </div>
  );
}

export default Bar;
